import React from "react";
import useData from "../../../hooks/useData";
import Item from "../../Item/Item";
import ItemsGrid from "../../UI/itemsGrid/ItemsGrid";
import classes from "./Series.module.scss";

function Films() {
  const { tvSeries } = useData();
  return (
    <>
      <section className="series">
        <h2 className={classes["title"]}>TV Series</h2>
        <ItemsGrid>
          {tvSeries.map((series, index) => (
            <Item data={series} key={index} />
          ))}
        </ItemsGrid>
      </section>
    </>
  );
}

export default Films;
